import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Store} from '@ngrx/store';
import {RealestateState} from '../store/realestate.reducer';
import {PropertyModel} from '../models/property.model';
import * as RealEstateActions from '../store/realestate.actions';
import {ActivatedRoute, ParamMap, Router} from '@angular/router';
import {ToastService} from '../../../../shared/toast/toast.service';
import {RealestateModel} from '../models/realestate.model';

@Component({
  selector: 'app-addpropertyre',
  templateUrl: './addpropertyre.component.html',
  styleUrls: ['./addpropertyre.component.css']
})
export class AddpropertyreComponent implements OnInit, OnDestroy {

  @ViewChild('t', {static: false}) tabSet;
  id: number;
  editMode = false;

  editForm: FormGroup;
  imageForm: FormGroup;
  pricingForm: FormGroup;
  realEstate: RealestateModel;

  image: File[] = [];
  previewImage: any = '../../../assets/icons/uploadPic.png';
  property = new PropertyModel();

  constructor(
    private fb: FormBuilder,
    private store: Store<RealestateState>,
    private router: Router,
    private route: ActivatedRoute,
    private toastService: ToastService
  ) {
  }

  ngOnInit() {
    this.editForm = this.fb.group({
      name: ['', Validators.required],
      listedBy: ['owner', Validators.required],
      location: ['', Validators.required],
      starRating: ['NONE', Validators.required],
      managerName: ['', Validators.required],
      managerEmail: ['', Validators.required],
      managerMobile: ['', Validators.required],
      landmark: ['', Validators.required],
      address: ['', Validators.required],
      postalCode: ['', Validators.required],

      plotSize: this.fb.group({
        size: ['', Validators.required],
        unit: ['sqft', Validators.required],
      }),

      contructedArea: this.fb.group({
        size: ['', Validators.required],
        unit: ['sqft', Validators.required],
      }),
      constructionYear: ['', Validators.required],
      lastRenovation: ['', Validators.required],
      otaChannel: ['', Validators.required],
      description: ['', Validators.required],
      facilities: this.fb.group({
        reception: ['', Validators.required],
        laundry: ['', Validators.required],
        powerBackup: ['', Validators.required],
        cctv: ['', Validators.required],
        mobileNetwork: ['', Validators.required],
        parking: ['', Validators.required],
        coldStorage: ['', Validators.required],
        guide: ['', Validators.required],
      }),
      amenities: this.fb.group({
        restaurants: ['', Validators.required],
        gym: ['', Validators.required],
        spa: ['', Validators.required],
        fitnessRoom: ['', Validators.required],
        swimmingPool: ['', Validators.required],
        lounge: ['', Validators.required],
        bonfire: ['', Validators.required],
        park: ['', Validators.required],
        fireplace: ['', Validators.required],
        otherAmenities: ['', Validators.required]
      }),
      activities: this.fb.group({
        waterPark: ['', Validators.required],
        adventurePark: ['', Validators.required],
        golf: ['', Validators.required],
        casino: ['', Validators.required],
        businessCentre: ['', Validators.required],
        gamesRoom: ['', Validators.required],
        tvRoom: ['', Validators.required],
        sunDeck: ['', Validators.required],

      }),
    });

    // only for img preview
    this.imageForm = this.fb.group({
      images: this.fb.array([]),
    });

    this.pricingForm = this.fb.group({
      rent: ['', Validators.required],
      electricityIncluded: ['', Validators.required],
      priceNegotiable: ['', Validators.required],
      securityDeposit: ['', Validators.required],
      maintenanceCharges: ['', Validators.required],
      bookingAmount: ['', Validators.required],
      rentAgreementDuration: ['', Validators.required],
      noticePeriod: ['', Validators.required],
    });

    this.store.select('realEstate').subscribe(
      value => {
        console.log(value);
        this.realEstate = value.realEstate;
        if (value.property) {
          this.property = value.property;
        }
      }
    );

    this.route.paramMap.subscribe(
      (params: ParamMap) => {
        if (params.get('id')) {
          this.id = +params.get('id');
          this.editMode = true;
          this.property = this.realEstate.properties[this.id];
          this.editForm.patchValue(this.realEstate.properties[this.id]);
          this.pricingForm.patchValue(this.realEstate.properties[this.id]);

          if (this.realEstate.properties[this.id].images) {
            this.image = this.realEstate.properties[this.id].images;
          }
        }
      }
    );
  }

  imgPreviewFeature(event) {
    const files = event.target.files;

    // only for img preview
    if (files) {
      for (const file of files) {
        this.image.push(file);
        const reader = new FileReader();
        reader.onload = (e: any) => {
          (this.imageForm.get('images') as FormArray).push(this.fb.group({
              url: e.target.result  // Base64 string for preview image
            }
          ));
        };
        reader.readAsDataURL(file);
      }
    }
  }

  onSubmit(clickCheck: boolean) {
    if (!this.editMode) {
      this.property._id = null;
      this.property.delFlg = false;
    }
    if (clickCheck) {
      this.dispatch(this.editForm.value);
      this.tabSet.select('pricing');
    } else {
      this.toastService.showDanger('Please Tick the checkbox First!');
    }

  }

  onImageSubmit() {
    if (this.image.length) {
      const data = new FormData();
      Array.from(this.image).forEach(file => {
        data.append('images[]', file);
      });

      data.append('_id', this.property._id);
      this.store.dispatch(new RealEstateActions.UploadRealEstatePropertyImages(data));
      if (this.editMode) {
        this.router.navigate(['../../dashboard'], {relativeTo: this.route});
      } else {
        this.router.navigate(['../dashboard'], {relativeTo: this.route});
      }
    } else {
      this.toastService.showDanger('Please Select Images');
    }
  }

  pricingSubmit() {
    this.dispatch(this.pricingForm.value);
    this.tabSet.select('imagesForm');
  }

  private dispatch(obj) {
    Object.assign(this.property, obj);
    this.store.dispatch(new RealEstateActions.PropertyEdit(this.property));

  }

  ngOnDestroy(): void {
    this.property = null;
  }
}
