import {Component, OnInit} from '@angular/core';
import * as RealestateActions from '../store/realestate.actions';
import {Store} from '@ngrx/store';
import {RealestateState} from '../store/realestate.reducer';
import {ActivatedRoute, Router} from '@angular/router';
import {PropertyModel} from '../models/property.model';
import * as RealEstateActions from '../store/realestate.actions';

@Component({
  selector: 'app-manageproperty',
  templateUrl: './manageproperty.component.html',
  styleUrls: ['./manageproperty.component.css']
})
export class ManagepropertyComponent implements OnInit {

  allProperties: PropertyModel[] = [];
  currentProperty: PropertyModel;

  constructor(private store: Store<RealestateState>, private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.store.dispatch(new RealestateActions.TryFetchRealEstate());

    this.store.select('realEstate').subscribe(
      value => {
        console.log('subscribed to realestate');
        return this.allProperties = value.realEstate.properties;
      });
  }

  editProperty(index) {
    this.router.navigate(['../editProperty', index], {relativeTo: this.route});
  }

  deleteProperty(index: number) {
    this.currentProperty = this.allProperties[index];
    this.currentProperty.delFlg = true;
    this.store.dispatch(new RealEstateActions.PropertyEdit(this.currentProperty));
    this.store.dispatch(new RealestateActions.TryFetchRealEstate());
  }

}
