import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {RealestateComponent} from './realestate.component';
import {RealestateeditComponent} from './realestateedit/realestateedit.component';
import {AddpropertyreComponent} from './addpropertyre/addpropertyre.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {ManagepropertyComponent} from './manageproperty/manageproperty.component';


const routes: Routes = [
  {
    path: '', component: RealestateComponent, children: [
      {path: 'dashboard', component: DashboardComponent},
      {path: 'editProfile', component: RealestateeditComponent},
      {path: 'manageProperty', component: ManagepropertyComponent},
      {path: 'editProperty/:id', component: AddpropertyreComponent},
      {path: 'addProperty', component: AddpropertyreComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RealestateRoutingModule {
}
