import {Component, OnInit} from '@angular/core';
import {Store} from '@ngrx/store';
import {ActivatedRoute, Router} from '@angular/router';
import {RealestateState} from './store/realestate.reducer';
import * as RealestateActions from './store/realestate.actions';
import {first} from 'rxjs/operators';

@Component({
  selector: 'app-realestate',
  templateUrl: './realestate.component.html',
  styleUrls: ['./realestate.component.css']
})
export class RealestateComponent implements OnInit {

  constructor(private store: Store<RealestateState>, private router: Router, private route: ActivatedRoute) {

  }

  ngOnInit() {
    this.store.dispatch(new RealestateActions.TryFetchRealEstate());

    this.store.select('realEstate').pipe(first()).subscribe(
      value => {
        if (value.realEstate.profilePercentage === 0 || value.realEstate.profilePercentage === undefined) {
          // alert('inside profile percentage zero');
          this.router.navigate(['editProfile'], {relativeTo: this.route});
        } else {
          this.router.navigate(['dashboard'], {relativeTo: this.route});
        }
      }
    );
  }

}
