import {Component, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Store} from '@ngrx/store';
import * as RealEstateActions from '../store/realestate.actions';
import {RealestateModel} from '../models/realestate.model';
import {RealestateState} from '../store/realestate.reducer';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-realestateedit',
  templateUrl: './realestateedit.component.html',
  styleUrls: ['./realestateedit.component.css']
})
export class RealestateeditComponent implements OnInit {

  @ViewChild('t', {static: false}) tabSet;

  navigationCheck = false;
  editForm: FormGroup;
  ownerForm: FormGroup;
  imageForm: FormGroup;

  registrationMsmeImage: File = null;
  registrationGstImage: File = null;
  registrationCinImage: File = null;

  previewImage: any = '../../../assets/icons/uploadPic.png';

  previewUrlCinProof: any = '../../../assets/icons/uploadPic.png';
  previewUrlregistrationMsmeNumber: any = '../../../assets/icons/uploadPic.png';
  previewUrlgstNumber: any = '../../../assets/icons/uploadPic.png';

  realEstateStore: RealestateModel;

  constructor(private fb: FormBuilder, private store: Store<RealestateState>, private router: Router, private route: ActivatedRoute) {

  }

  ngOnInit() {

    this.imageForm = this.fb.group({
      registrationCinProof: [''],
      gstNumberProof: [''],
      registrationMsmeNumberProof: ['']
    });

    this.ownerForm = this.fb.group({
      ownerDetails: this.fb.group({
        profileRegistrar: ['OWNER', Validators.required],
        name: ['', Validators.required],
        mobile: ['', Validators.required],
        email: ['', Validators.required],
        pan: ['', Validators.required],
        address: this.fb.group({
          address: [''],
          pincode: [''],
          phoneNo: ['']
        })
      }),
      bankDetails: this.fb.group({
        bankName: [''],
        bankAccountNo: [''],
        bankIfsc: [''],
        bankAddress: ['']
      }),
    });


    this.editForm = this.fb.group({

      name: ['', Validators.required],
      registrationType: ['privateLimited', Validators.required],
      registrationNo: ['', Validators.required],
      officeAddress: ['', Validators.required],
      officePostalCode: ['', Validators.required],
      registrationAuthority: ['', Validators.required],
      registrationMsmeNumber: ['', Validators.required],
      gstNumber: ['', Validators.required],
      panNumber: ['', Validators.required],
      tanNumber: ['', Validators.required],
      unionEnrollmentNumber: ['', Validators.required],
      mobile: ['', Validators.required]
    });

    this.store.select('realEstate').subscribe(
      value => {
        console.log(value);
        this.realEstateStore = value.realEstate;

        this.previewUrlCinProof = value.realEstate.registrationCinProof !== undefined ? value.realEstate.registrationCinProof : this.previewUrlCinProof;
        this.previewUrlgstNumber = value.realEstate.gstNumberProof !== undefined ? value.realEstate.gstNumberProof : this.previewUrlregistrationMsmeNumber;
        this.previewUrlregistrationMsmeNumber = value.realEstate.registrationMsmeNumberProof !== undefined ? value.realEstate.registrationMsmeNumberProof : this.previewUrlgstNumber;
        this.editForm.patchValue(this.realEstateStore);
        this.imageForm.patchValue(this.realEstateStore);

        if (value.realEstate.ownerDetails !== undefined) {
          this.ownerForm.patchValue(this.realEstateStore);
        }

      }
    );
  }


  imgPreviewregistrationMsmeNumber(fileInput: any) {
    this.registrationMsmeImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationMsmeImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationMsmeImage);
    reader.onload = () => {
      this.previewUrlregistrationMsmeNumber = reader.result;
    };
  }

  imgPreviewgstNumber(fileInput: any) {
    this.registrationGstImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationGstImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationGstImage);
    reader.onload = () => {
      this.previewUrlgstNumber = reader.result;
    };
  }

  imgPreviewaddress(fileInput: any) {
    this.registrationCinImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationCinImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationCinImage);
    reader.onload = () => {
      this.previewUrlCinProof = reader.result;
    };
  }

  onImageSubmit() {
    const data = new FormData();
    data.append('registrationCinProof', this.registrationCinImage);
    data.append('gstNumberProof', this.registrationGstImage);
    data.append('registrationMsmeNumberProof', this.registrationMsmeImage);

    this.store.dispatch(new RealEstateActions.UploadRealEstateImages(data));
  }

  onOwnerSubmit() {
    this.dispatch(this.ownerForm.value);
    this.tabSet.select('imagesForm');
  }

  onSubmit() {
    this.dispatch(this.editForm.value);
    this.tabSet.select('ownerDetails');
  }

  private dispatch(obj) {
    Object.assign(this.realEstateStore, obj);
    this.store.dispatch(new RealEstateActions.RealEstateEdit(this.realEstateStore));
  }

  navigationTrigger() {
    this.navigationCheck = true;
  }
}
