import {RealestateActions} from './realestate.actions';
import {RealestateModel} from '../models/realestate.model';
import {AppState} from '../../../../store/app.reducer';
import {PropertyModel} from '../models/property.model';

export interface RealestateState extends AppState {
  realEstate: State;
}

export interface State {
  realEstate: RealestateModel;
  property: PropertyModel;
}

const initialState: State = {
  realEstate: new RealestateModel(),
  property: new PropertyModel()
};

export function RealEstateReducer(state = initialState, action: RealestateActions) {
  switch (action.type) {
    case 'SET_REALESTATE': {
      return {
        ...state,
        realEstate: action.payload
      };
    }
    case 'SET_REALESTATE_PROPERTY': {
      return {
        ...state,
        property: action.payload
      };
    }
    default: {
      return {
        ...state
      };
    }
  }
}
