import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {map, switchMap} from 'rxjs/operators';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../../shared/serverEndpoints';
import * as RealestateActions from './realestate.actions';
import {PropertyModel} from '../models/property.model';
import {ToastService} from '../../../../shared/toast/toast.service';

@Injectable()
export class RealestateEffects {
  constructor(public action: Actions, public http: HttpClient, private toastService: ToastService) {
  }

  @Effect()
  uploadPropertyImage = this.action.pipe(
    ofType(RealestateActions.UPLOAD_PROPERTY_IMAGES),
    map((data: RealestateActions.UploadRealEstatePropertyImages) => {
      return data.payload;
    }),
    switchMap((data) => {
      return this.http.post(endPoints.uploadrealestatePropertyImages, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        this.toastService.showSuccess('Voila! Images Uploaded Successfully!');
        return [
          {
            type: RealestateActions.SET_PROPERTY,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

// effects for realestate
  @Effect()
  editProperty = this.action.pipe(
    ofType(RealestateActions.PROPERTY_EDIT),
    map((payload: RealestateActions.PropertyEdit) => {
      return payload.payload;
    }),
    switchMap((data: PropertyModel) => {
      return this.http.post(endPoints.saverealEstateProperty, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      this.toastService.showSuccess('Thanks for updating your Property Details!');
      if (response.status === 200) {
        return [
          {
            type: RealestateActions.SET_PROPERTY,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );


// effects for realestate
  @Effect()
  editRealEstate = this.action.pipe(
    ofType(RealestateActions.REALESTATE_EDIT),
    map((payload: RealestateActions.RealEstateEdit) => {
      return payload.payload;
    }),
    switchMap((data) => {
      return this.http.post(endPoints.editrealestate, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        this.toastService.showSuccess('Thanks for updating your Real Estate Company Details!');
        return [
          {
            type: RealestateActions.SET_REALESTATE,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );


  @Effect()
  uploadRealEstateImages = this.action.pipe(
    ofType(RealestateActions.UPLOAD_REALESTATE_IMAGES),
    map((data: RealestateActions.UploadRealEstateImages) => {
      return data.payload;
    }), switchMap((data: { registrationCinImage: File, registrationGstImage: File, registrationMsmeImage: File, registrationRcImage: File }) => {
      return this.http.post(endPoints.uploadrealestateImages, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        this.toastService.showSuccess('Voila! Images Uploaded Successfully!');
        return [
          {
            type: RealestateActions.SET_REALESTATE,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  fetchRealEstate = this.action.pipe(
    ofType(RealestateActions.TRY_FETCH_REALESTATE),
    switchMap(() => {
      return this.http.get(endPoints.fetchrealestate, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: RealestateActions.SET_REALESTATE,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

}
