// realestate action
import {Action} from '@ngrx/store';
import {RealestateModel} from '../models/realestate.model';
import {PropertyModel} from '../models/property.model';

export const TRY_FETCH_REALESTATE = 'TRY_FETCH_REALESTATE';
export const SET_REALESTATE = 'SET_REALESTATE';
export const REALESTATE_EDIT = 'REALESTATE_EDIT';
export const UPLOAD_REALESTATE_IMAGES = 'UPLOAD_REALESTATE_IMAGES';
export const SET_PROPERTY = 'SET_REALESTATE_PROPERTY';
export const UPLOAD_PROPERTY_IMAGES = 'UPLOAD_REALESTATE__PROPERTY_IMAGES';
export const PROPERTY_EDIT = 'PROPERTY_EDIT';

export class PropertyEdit implements Action {
  readonly type = PROPERTY_EDIT;

  constructor(public payload: PropertyModel) {
  }
}

export class UploadRealEstatePropertyImages implements Action {
  readonly type = UPLOAD_PROPERTY_IMAGES;

  constructor(public payload: any) {
  }
}


export class SetRealEstateProperty implements Action {
  readonly type = SET_PROPERTY;

  constructor(public payload: PropertyModel[]) {
  }
}

// classes for realestate
export class TryFetchRealEstate implements Action {
  readonly type = TRY_FETCH_REALESTATE;
}

export class SetRealEstate implements Action {
  readonly type = SET_REALESTATE;

  constructor(public payload: RealestateModel) {
  }
}

export class UploadRealEstateImages {
  readonly type = UPLOAD_REALESTATE_IMAGES;

  constructor(public payload: any) {
  }
}

export class RealEstateEdit implements Action {
  readonly type = REALESTATE_EDIT;

  constructor(public payload: RealestateModel) {
  }
}

export type RealestateActions = SetRealEstateProperty | UploadRealEstatePropertyImages | PropertyEdit |
  RealEstateEdit |
  UploadRealEstateImages |
  SetRealEstate |
  TryFetchRealEstate;
